  white-space: nowrap;
}
.message-list li .col-mail-2 .date {
  right: 0;
  width: 170px;
  padding-left: 80px;
}
.message-list li.active, .message-list li.active:hover {
  box-shadow: inset 3px 0 0 #0bb197;
}
.message-list li.unread {
  background-color: var(--bs-tertiary-bg);
  font-weight: 500;
  color: var(--bs-emphasis-color);
}
.message-list li.unread a {
  color: var(--bs-emphasis-color);
  font-weight: 500;
}
.message-list .checkbox-wrapper-mail {
  cursor: pointer;
  height: 20px;
  width: 20px;
  position: relative;
  display: inline-block;
  box-shadow: inset 0 0 0 1px var(--bs-secondary-color);
  border-radius: 1px;
}
.message-list .checkbox-wrapper-mail input {
  opacity: 0;
  cursor: pointer;
}
.message-list .checkbox-wrapper-mail input:checked ~ label {
  opacity: 1;
}
.message-list .checkbox-wrapper-mail label {
  position: absolute;
  height: 20px;
  width: 20px;
  left: 0;
  cursor: pointer;
  opacity: 0;
  margin-bottom: 0;
  transition-duration: 0.05s;
  top: 0;
}
.message-list .checkbox-wrapper-mail label:before {
  content: "\f012c";
  font-family: "Material Design Icons";
  top: 0;
  height: 20px;
  color: var(--bs-emphasis-color);
  width: 20px;
  position: absolute;
  margin-top: -16px;
  left: 4px;
  font-size: 13px;
}

@media (max-width: 575.98px) {
  .message-list li .col-mail-1 {
    width: 200px;
  }
}
.counter-number {
  font-size: 32px;
  font-weight: 500;
  text-align: center;
}
.counter-number span {
  font-size: 16px;
  font-weight: 400;
  display: block;
  padding-top: 7px;
}

.coming-box {
  float: left;
  width: 21%;
  padding: 14px 7px;
  background-color: var(--bs-secondary-bg);
  border-radius: 7px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.08);
  margin: 15px;
}

@media (max-width: 991.98px) {
  .coming-box {
    width: 40%;
  }
}
/************** vertical timeline **************/
/************** Horizontal timeline **************/
.slick-slide {
  margin: 10px 12px;
  transition: all ease-in-out 0.3s;
  opacity: 0.2;
  padding: 7px;
  border-radius: 4px;
}
.slick-slide:focus {
  outline: none;
}

.slick-prev:before,
.slick-next:before {
  color: var(--bs-secondary-color);
}

.slick-active {
  opacity: 1;
}

.slick-slider.hori-timeline-desc {
  padding: 12px 24px 0;
  text-align: center;
  border-radius: 7px;
}
.slick-slider.hori-timeline-nav .slick-active {
  opacity: 0.5;
}
.slick-slider.hori-timeline-nav .slick-current {
  opacity: 1;
  background-color: rgba(11, 177, 151, 0.2);
}
.slick-slider.hori-timeline-nav .slick-current .nav-title {
  color: #0bb197;
}
.slick-slider.hori-timeline-nav .slider-nav-item {
  text-align: center;
}
.slick-slider.fade:not(slow) {
  opacity: 1;
}

/************** vertical timeline **************/
.verti-timeline {
  border-left: 2px solid var(--bs-border-color);
  margin: 0 5px;
}
.verti-timeline .event-list {
  position: relative;
  padding: 0px 0px 25px 30px;
}
.verti-timeline .event-list:before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  left: -2px;
  top: 8px;
  border-top: 6px double var(--bs-border-color);
  border-left: 2px solid var(--bs-border-color);
  border-top-left-radius: 15px;
}
.verti-timeline .event-list .event-content {
  position: relative;
  border: 2px solid var(--bs-border-color);
  border-radius: 7px;
}
.verti-timeline .event-list.active .event-timeline-dot {
  color: #0bb197;
}
.verti-timeline .event-list:last-child {
  padding-bottom: 0px;
}

/*********************
    Faqs
**********************/
.gallery-box a .gallery-content {
  border-radius: 4px;
  overflow: hidden;
}
.gallery-box a .gallery-overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #0bb197;
  opacity: 0;
  transition: all 0.5s;
}
.gallery-box a .overlay-content {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -60px;
  background-color: var(--bs-secondary-bg);
  transition: all 0.5s;
  padding: 16px 12px 12px 12px;
  margin: 0px 12px;
  border-radius: 4px;
}
.gallery-box a .gallery-overlay-icon {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-85%);
  transition: all 0.5s;
}
.gallery-box a .gallery-overlay-icon i {
  font-size: 28px;
}
.gallery-box a:hover .gallery-overlay {
  opacity: 0.7;
}
.gallery-box a:hover .overlay-content {
  position: absolute;
  bottom: 12px;
}

.error-text {
  text-shadow: 4px 4px rgba(11, 177, 151, 0.4);
}